export default angular.module('deskworks.AppointmentPlusSettings', [
  'ngResource',
  'deskworks.config',
])

  .factory('AppointmentPlusSettings', function($resource, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/appointment-plus/settings'), {}, {
      get: { method: 'GET' },
      update: { method: 'PUT' },
    });

    return {
      get: () => res.get().$promise,

      save: data => res.update({}, data).$promise,
    };
  });
