/**
 * Integrations Setup -> Appointment Plus Settings directive
 */
export default angular.module('deskworks.appointmentPlusSettingsDirective', [
  'debounce',
  'deskworks.AppointmentPlusSettings',
])

  .directive('appointmentPlusSettings', function() {
    const templateUrl = 'settings/appointment-plus-settings.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'AppointmentPlusSettingsCtrl as $ctrl',
    };
  })

  .controller('AppointmentPlusSettingsCtrl', function(dwAlerts, debounce, AppointmentPlusSettings) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.load();
    };

    /**
     * Load settings
     */
    $ctrl.load = () => {
      $ctrl.spin = true;
      return AppointmentPlusSettings.get()
        .catch(err => dwAlerts.error(err, 'Failed to load AppointmentPlus settings.'))
        .finally(() => { $ctrl.spin = false; })
        .then(settings => { $ctrl.settings = settings; });
    };

    /**
     * Save settings
     */
    $ctrl.save = debounce(save, 1000);
    function save(form) {
      if (form.$invalid) return;
      $ctrl.spin = true;
      return AppointmentPlusSettings.save($ctrl.settings)
        .catch(err => dwAlerts.error(err, 'Failed to save AppointmentPlus settings.'))
        .finally(() => { $ctrl.spin = false; });
    }
  });
